import { GatsbyImage, getImage, StaticImage } from 'gatsby-plugin-image';
import React, { useEffect, useState } from 'react';
import AagnesLogoWhite from '../images/aagnes_logo_white.svg'
import { waitFor } from '../utils/Generic';

const HeroBanner = ({images}) => {

  const [currentImage, setCurrentImage] = useState(-1)

  const [topSrc, setTopSrc] = useState()
  const [bottomSrc, setBottomSrc] = useState()
  const [topVisible, setTopVisible] = useState(false)
  const [logoVisible, setLogoVisible] = useState(false)
  
  useEffect(async () => {
    await waitFor(1500)
    setCurrentImage(currentImage => currentImage >= images.length - 1 ? 0 : currentImage + 1)
    setLogoVisible(true)
    await waitFor(2500)
    setInterval(() => {
      setCurrentImage(currentImage => currentImage >= images.length - 1 ? 0 : currentImage + 1)
    }, 6000);
  }, [])

  useEffect(() => {
    const setImageSources = async () => {
      setTopSrc(getImage(images[currentImage]?.node.childImageSharp))
      await waitFor(2500)
      setTopVisible(true)
      await waitFor(2000)
      setBottomSrc(getImage(images[currentImage]?.node.childImageSharp))
    }

    setImageSources()
  }, [currentImage])

  useEffect(() => {
    setTopVisible(false)
  }, [bottomSrc])

  return(
    <div className="relative h-screen bg-black overflow-hidden">
      <div className='absolute h-full w-full'>
        {/* bottom */}
        <div className='absolute h-full'>
          <GatsbyImage
            image={bottomSrc}
            loading='eager'
            imgStyle={{
              objectFit: "cover",
            }}
            className="h-full"
            alt=""
          />
        </div>
        {/* top */}
        <div 
          className={`absolute h-full transform ${topVisible ? 'transition-opacity' : 'transition-none'} duration-2000 ${topVisible ? 'opacity-100' : 'opacity-0'}`}
        >
          <GatsbyImage
            image={topSrc}
            loading='eager'
            imgStyle={{
              objectFit: "cover",
            }}
            className="h-full"
            alt=""
          />
        </div>
      </div>
      <div className={`absolute h-full flex justify-start items-center transform transition-opacity duration-1000 ${logoVisible ? 'opacity-100' : 'opacity-0'}`}>
        <img
          src={AagnesLogoWhite}
          width={200}
          className="ml-4 lg:ml-8"
          alt="Aagnes"
        />
      </div>
    </div>
  )
}

export default HeroBanner;